/* Shimmer animation cleaned up - no longer used */
/* Product page button pulse animation - matching screen-time-journey-workspace */
.product-pulse-button {
    animation: pulse-heartbeat 6.5s ease-in-out infinite !important;
    position: relative;
    overflow: hidden;
  }
.product-pulse-button::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    animation: shimmer 6s ease-in-out infinite;
    animation-delay: 1s;
    pointer-events: none;
  }
@keyframes pulse-heartbeat {
    0% { transform: scale(1); }
    7.5% { transform: scale(1.05); }
    15% { transform: scale(1); }
    15.1%, 100% { transform: scale(1); }
  }
@keyframes shimmer {
    0% { left: -100%; }
    100% { left: 100%; }
  }
@keyframes slideUpFixed {
    from {
      transform: translateX(-50%) translateY(100px);
      opacity: 0;
    }
    to {
      transform: translateX(-50%) translateY(0);
      opacity: 1;
    }
  }
/* Sticky Cart Button Effects - Using DBTFY Theme Pattern */
.sticky-cart-button {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    isolation: isolate;
  }
.sticky-cart-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f0f0f0;
    border-radius: 6px;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.5s ease, transform-origin 0s;
    z-index: -1;
  }
.sticky-cart-button:hover::before {
    transform: scaleX(1);
    transform-origin: left;
  }
.sticky-cart-button:not(:hover)::before {
    transform: scaleX(0);
    transform-origin: right;
  }
.sticky-cart-button:hover {
    color: var(--brand-primary);
  }

