/* === HEADER STYLES FROM TEMPLATE_REFERENCE_2 === */
:root {
  --brand-primary: #2E0456;
  --brand-primary-dark: #1a0230;
  --brand-text: #0F172A;
  --brand-background: #FFFFFF;
  --brand-separator: #E5E5E5;
  --page-bg: #f9f9f9;
  --text-muted: #5b6472;
  --font-heading: 'DM Serif Display', serif;
  --font-body: 'Inter', sans-serif;
  --radius-sm: 6px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --shadow-sm: 0 2px 4px rgba(15, 23, 42, 0.04);
  --shadow-md: 0 4px 12px rgba(15, 23, 42, 0.08);
  --shadow-lg: 0 8px 24px rgba(15, 23, 42, 0.12);
  --shadow-brand: 0 4px 12px rgba(46, 4, 86, 0.2);
  --mobile-spacing-sm: 12px;
}

.header {
  /* Position and styling now handled via Tailwind classes in JSX */
  padding: 0;
  transition: transform 0.15s ease-out; /* Only smooth transition for hide/show effect */
  border-bottom: 1px solid #EEEEEE !important;
  min-height: 80px;
  height: 80px;
}

/* Normal header: shrink to 80px on scroll */
.header.scrolled {
  min-height: 80px;
  height: 80px;
}

/* Transparent header is always 80px high */
.header-transparent {
  min-height: 80px !important;
  height: 80px !important;
}

/* Transparent header stays 80px even when scrolled */
.header-transparent.scrolled {
  min-height: 80px !important;
  height: 80px !important;
}

/* Remove desktop-specific overrides - let original CSS handle desktop layout */

/* Duplicate section removed - consolidated in main mobile block around line 3355 */

/* Mobile grid layout for transparent header */
.header-transparent .header-inner {
  height: 80px;
  padding: 0 59px;
}

/* Shopify header appear effect - with mobile compatibility */
.shopify-section-header-hidden {
  transform: translateY(-100%) !important;
}

.shopify-section-header-sticky.animate {
  transform: translateY(0) !important;
  animation: slideDown 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes slideDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

/* Ensure mobile header scroll animation works properly */
@media (max-width: 1024px) {
  .header.shopify-section-header-hidden {
    transform: translateY(-100%) !important;
  }
  
  .header.shopify-section-header-sticky.animate {
    transform: translateY(0) !important;
  }
}

.header.scrolled {
  padding: 0;
}

.header-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 59px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 80px;
  height: 80px;
  position: relative;
  transition: min-height 0.3s cubic-bezier(0.4, 0, 0.2, 1), height 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Allow layout transitions but disable text/button flashing */
.header-transparent .header-inner,
.header-transparent.scrolled .header-inner {
  transition: height 0.15s ease-out, padding 0.15s ease-out; /* Allow smooth layout changes */
}

/* Disable color/background transitions to prevent flashing */
.header-transparent .header-nav a,
.header-transparent.scrolled .header-nav a,
.header-transparent .header-buttons-desktop *,
.header-transparent.scrolled .header-buttons-desktop * {
  transition: none !important; /* Prevent color flash */
}

.header.scrolled .header-inner {
  min-height: 80px;
  height: 80px;
}

/* Transparent header inner has 80px height */
.header-transparent .header-inner {
  min-height: 80px;
  height: 80px;
}

/* Desktop header wrapper for nav + actions - keep normal layout */
.header-desktop-wrapper {
  display: flex;
  align-items: center;
  gap: 2rem;
  justify-content: space-between; /* Keep buttons on the right */
}

/* Logo container needs to stack images */
.header-logo {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  padding: 6px;
  position: relative; /* Added for stacking */
  transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1), height 0.5s cubic-bezier(0.4, 0, 0.2, 1), padding 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.header-logo img {
  height: 100%;
  max-height: 80px;
  width: auto;
  display: block;
  transition: max-height 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Stack both logos on top of each other */
.header-logo .logo-dark,
.header-logo .logo-inverted {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  max-height: 80px;
  width: auto;
  display: block;
  pointer-events: none; /* Prevent click interference */
  transition: none !important; /* No transitions = no flash */
}

/* Default state: Show dark logo */
.header-logo .logo-dark {
  opacity: 1;
}

.header-logo .logo-inverted {
  opacity: 0;
}

/* Transparent header: Show inverted logo */
.header-transparent .header-logo .logo-dark {
  opacity: 0 !important;
}

.header-transparent .header-logo .logo-inverted {
  opacity: 1 !important;
}

/* Scrolled transparent header: Show dark logo again */
.header-transparent.scrolled .header-logo .logo-dark {
  opacity: 1 !important;
}

.header-transparent.scrolled .header-logo .logo-inverted {
  opacity: 0 !important;
}

/* Normal header: stays same size when scrolled */
.header.scrolled .header-logo {
  width: 80px;
  height: 80px;
  padding: 6px;
}

.header.scrolled .header-logo img {
  max-height: 60px;
}

/* Transparent header: logo stays same */
.header-transparent .header-logo img {
  max-height: 80px;
}

/* Shopify-style logo effect - simplified approach */

/* Default logo state */
.header-logo {
  transform: scale(1);
  transition: transform 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Classes that will be added via JavaScript for animations */
.header-logo.logo-animate-shrink {
  animation: logoShrinkEffect 0.4s cubic-bezier(0.1, 0, 0.9, 1) forwards; /* Faster + accelerating */
}

.header-logo.logo-animate-grow {
  animation: logoGrowEffect 0.35s cubic-bezier(0.1, 0, 0.9, 1) forwards; /* Faster + accelerating */
}

.header-transparent .header-logo img,
.header-transparent.scrolled .header-logo img {
  max-height: 80px;
}

/* Animation: Logo shrinks from RIGHT to LEFT (scroll down) */
@keyframes logoShrinkEffect {
  0% {
    transform: scale(1.3) translateX(8px); /* Starts big + shifted right */
  }
  50% {
    transform: scale(1.15) translateX(3px); /* Mid-shrink + moving left */
  }
  100% {
    transform: scale(1) translateX(0px); /* Ends normal + centered */
  }
}

/* Animation: Logo grows from LEFT to RIGHT (scroll up) */
@keyframes logoGrowEffect {
  0% {
    transform: scale(0.7) translateX(-8px); /* Starts small + LEFT (opposite of shrink) */
  }
  50% {
    transform: scale(0.85) translateX(-3px); /* Mid-grow + moving RIGHT */
  }
  100% {
    transform: scale(1) translateX(0px); /* Ends normal + centered */
  }
}

/* Desktop navigation positioning */
@media (min-width: 1025px) {
  .header-nav {
    flex: none;
    display: flex !important;
    gap: 20px !important;
    align-items: center !important;
    justify-content: flex-start !important;
    padding-left: 0px !important;
    position: absolute !important;
    left: 180px !important; /* Increased spacing between logo and nav items */
    top: 50% !important;
    transform: translateY(-50%) !important;
    z-index: 5 !important;
  }
}

.header-nav a {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #0F172A;
  text-decoration: none;
  letter-spacing: 0.3px;
  transition: all 0.2s ease;
  white-space: nowrap;
}

/* Targeted transition disables - allow layout animations, prevent color flashing */
.header-transparent .header-nav a,
.header-transparent.scrolled .header-nav a,
.header-transparent .header-buttons-desktop *,
.header-transparent.scrolled .header-buttons-desktop * {
  transition: none !important; /* Prevent text/button color flashing */
}

/* Allow smooth logo transitions - match default header timing */
.header-transparent .header-logo,
.header-transparent.scrolled .header-logo {
  transition: width 0.3s ease, height 0.3s ease, padding 0.3s ease !important;
}

/* Force white text in transparent header */
header.bg-transparent .header-nav a,
