.mobile-menu-dropdown {
  position: fixed;
  top: calc(42px + 80px); /* Start immediately at header bottom: announcement (42px) + header (80px) */
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background: #f9f9f9;
  z-index: 100;
  overflow-y: auto;
