/* Responsive - Mobile Design */
@media (max-width: 768px) {
  .stj-milestone-preview-container,
  .stj-leaderboard-preview-container {
    padding: 0 40px;
  }
  
  .stj-milestone-cards {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    margin: 2rem 0;
  }
  
  .stj-milestone-preview-card {
    padding: 20px;
