}

/* About Page Specific Styles */
.about-content-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.about-content-block {
  background: #f9fafb;
  padding: 2rem;
  border-radius: 6px;
  border: 1px solid #e5e7eb;
}

.about-content-block h3 {
  margin-top: 0;
  color: var(--brand-primary);
}

